.projects-container {
    position: relative;
    width: 1320px;
    background-color: #575e95;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    flex-wrap: wrap;
    overflow-x: hidden;
}

@media (max-width: 480px) {
    .projects-container {
        width: 100%;
    }
}

