@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(112, 115, 132);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --clr1: #151D3C
}
.App {
  position: relative;
  background-color: #151D3C;
  background-color: var(--clr1);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}


.title_container {
    position: relative;
    height: 800px;
    width: 1320px;
    background-color: #151D3C;
    overflow: hidden;
}

.title_container h1 {
    position: absolute;
    width: 276px;
    height: 168px;
    left: 160px;
    top: 150px;
  
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 72px;
    line-height: 84px;
    letter-spacing: 0.2em;
    text-align: center;
    text-shadow: 1px 1px 1px black;
  
    color: white;
    z-index: 1;
    cursor: default;
}

.title_container #main-portrait {
    position: absolute;
    height: 700px;
    bottom: 0px;
    left: 400px;
    z-index: 1;
    pointer-events: none;
}

.title_container #logo {
    position: absolute;
    top: 10px;
    left: 0;
    height: 100px;
}

.title_container #rect1 {
    position: absolute;
    bottom: 230px;
    left: 230px;
    width: 107px;
    height: 12px;
    background-color: #021DA8;
}

.title_container #diamond {
    position: absolute;
    height: 200px;
    width: 200px;
    border-radius: 20px;
    transform: rotate(-30deg);
    top: 100px;
    right: 100px;
    background: rgba(255, 198, 26, 0.8);
    z-index: 0;
    animation: spin 20s infinite linear;
}
  
@keyframes spin {
    from { 
        transform: rotateZ(0deg) rotate(-45deg); 
    }
    to {
        transform: rotateZ(360deg) rotate(-45deg); 
    }
}

.title_container #diamond2 {
    position: absolute;
    height: 350px;
    width: 350px;
    border-radius: 50px;
    top: 110px;
    left: 110px;
    background: rgba(255, 198, 26, 0.8);
    z-index: 0;
    animation: spin2 30s infinite linear;
}

@keyframes spin2 {
    from { 
        transform: rotateZ(360deg) rotate(-45deg); 
    }
    to {
        transform: rotateZ(0deg) rotate(-45deg); 
    }
}




@media (max-width: 480px) {
    .title_container {
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
    }

    .title_container h1 {
        position: relative;
        height: 168px;
        left: 0px;
        right: 0px;
        top: 0px;
      
        font-size: 64px;
        margin-top: 110px;
    }

    .title_container #main-portrait {
        position: absolute;
        height: 400px;
        bottom: 0px;
        left: 50%;
        transform: translateX(-50%);
        opacity: 0.5;
        z-index: 0;
    }

    .title_container #diamond2 {
        display: none;
    }

    .title_container #rect1 {
        position: relative;
        bottom: 0px;
        left: 0px;
    }
}


  
.social-container {
    position: absolute;
    left: 175px;
    bottom: 150px;
    width: 220px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    overflow-x: hidden;
    z-index: 1;
}

.social-container a {
    height: 64;
    width: 64;
    transition: all 0.3s ease-in-out
}

.social-container a:hover {
    transform: scale(1.2);
}


@media (max-width: 480px) {
    .social-container {
        position: relative;
        left: 0;
        bottom: 0;
        right: 0;
        top: 0;
    }
}
.intro-container {
    position: absolute;
    right: 100px;
    top: 200px;
    color: white;
    font-family: Roboto;
    overflow-x: hidden;
}

.intro-container #top {
    font-size: 18px;
    color: white;
    cursor: default;
}
    
.intro-container #middle {
    width: 400px;
    font-size: 48px;
    letter-spacing: 0.1em;
    color: white;
    cursor: default;
}

.intro-container #bottom {
    width: 410px;
    font-size: 18px;
    font-style: normal;
    font-weight: normal;
    letter-spacing: 0.1em;
    line-height: 30px;
    color: white;
    cursor: default;
}

.intro-container a {
    font-size: 48px;
    color: rgba(192, 201, 249, 1);
}

@media (max-width: 480px) {
    .intro-container {
        position: relative;
        right: 0;
        top: 0;
        width: 100%;
    }

    .intro-container #top {
        padding: 10px;
    }

    .intro-container #middle {
        width: 100%;
        padding: 10px;
        font-size: 36px;
    }

    .intro-container #bottom {
        width: 100%;
        padding: 10px;
        margin-bottom: 200px;
    }
}

.nav-bar {
    position: absolute;
    top: 35px;
    right: 50px;
    width: 300px;
    z-index: 2;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.nav-bar a {
    color: white;
    font-family: 'Roboto';
    font-size: 18px;
    text-decoration: none;
    transition: all 0.3s;
}

.nav-bar a:hover {
    transform: scale(1.1);
    color: rgb(20, 197, 220)
} 

.projects-container {
    position: relative;
    width: 1320px;
    background-color: #575e95;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    flex-wrap: wrap;
    overflow-x: hidden;
}

@media (max-width: 480px) {
    .projects-container {
        width: 100%;
    }
}


:root {
    --level-one: translateZ(20px);
    --level-two: translateZ(40px);
    --level-three: translateZ(60px);
}

.project-card {
    width: 300px;
    height: 350px;
    margin: 50px;
    opacity: 1;
    transition: all 1s ease-in-out;
}

.project-card.offscreen {
    opacity: 0;
    transform: translateX(100%);
}

.project-card .card__content {
    height: 100%;
    width: 100%;
    text-align: center;
    position: relative;
    align-items: center;
    transition: transform 1.5s;
    transform-style: preserve-3d;
}

.project-card:hover .card__content {
   transform: rotateY(.5turn);
}

.project-card .card___front,
.project-card .card__back {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    transform-style: preserve-3d;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

.project-card .card__front {
    height: 100%;
    display: flex;
    border-radius: 38px 0 38px 0;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.project-card .card__back {
    background: #151D3C;
    border-radius: 0 38px 0 38px;
    padding: 10px;
    transform: rotateY(180deg);
}


.project-card .card__front .bg-img {
    position: absolute;
    height: 100%;
    width: 100%;
    border-radius: 38px 0 38px 0;
    background: rgba(166, 161, 238);
    overflow: hidden;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

.project-card .card__front .bg-img img {
    height: 100%;
    width: 100%;
    opacity: 0.2;
}

.project-card .card__front::before {
    position: absolute;
    content: '';
    height: 85%;
    width: 85%;
    border-radius: 10px;
    border: 3px solid black;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    transform: translateZ(20px);
    transform: var(--level-one);
}

.project-card .card__front .title {
    height: 80px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transform: translateZ(60px);
    transform: var(--level-three);
}

.project-card .card__front .techs-list {
    width: 90%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    transform: translateZ(40px);
    transform: var(--level-two);
}

.project-card .card__back .links-list {
    transform: translateZ(60px);
    transform: var(--level-three);
}

.project-card .card__back p {
    font-weight: bold;
    transform: translateZ(40px);
    transform: var(--level-two);
    color: white;
    cursor: default; 
}

.project-card .card__back h2 {
    color: white;
    transform: translateZ(60px);
    transform: var(--level-three);
    cursor: default; 
}

.project-card .card__back li {
    font-size: 14px;
    color: white;
    transform: translateZ(20px);
    transform: var(--level-one);
    cursor: default; 
}

.project-card .card__back .links-list {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

.project-card .card__back .links-list a {
    text-decoration: none;
    font-weight: bold;
    font-size: 24px;
    color: white;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.50);
    transition: all .3s ease-in-out;
}

.project-card .card__back .links-list a:hover {
    transform: scale(1.2);
    color: rgb(20, 197, 220);
}
.contact-container {
    width: 1320px;
    height: 600px;
    margin-bottom: 50px;
    background-color: #151D3C;
    border-radius: 0 0 67px 67px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 2;
    overflow-x: hidden;
}

@media (max-width: 480px) {
    .contact-container {
        width: 100%;
    }
}
.contact-form {
    position: relative;
    width: 400px;
}

.contact-form .input-div {
    position: relative;
}

.contact-form .input-div input{
    width: 100%;
    padding: 10px;
    font-size: 18px;
    background: transparent;
    color: white;
    border-radius: 0;
    border: none;
    border-bottom: 3px solid white;
}

.contact-form .input-div input:focus {
    outline: none;
}

.contact-form .input-div label {
    position: absolute;
    top: 20px;
    left: 10px;
    color: #fff;
    font-size: 1.2em;
    pointer-events: none;
    transition: .5s;
}

.contact-form .input-div .active {
    color: rgb(20, 197, 220);
    top: 0px;
    left: 5px;
    font-size: 14px;
}

.contact-form label {
    position: relative;
    left: 10px;
    color: white;
    font-size: 18px;
}
.contact-form textarea {
    font-family: 'Roboto';
    font-size: 18px;
    resize: none;
    width: 100%;
    height: 200px;
    padding: 10px;
    color: white;
    border: 2px solid white;
    border-radius: 5px;
    background: transparent;
}

@media (max-width: 480px) {
    .contact-form {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .contact-form textarea {
        width: 90%;
    }

    .contact-form .input-div {
        width: 100%;
    }

    .contact-form .input-div input{
        width: 90%;
    }

}


.story-container {
    position: relative;
    width: 1320px;
    background-color: var(--clr1);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    overflow-x: hidden;
}

@media (max-width: 480px) {
    .story-container {
        width: 100%;
    }
}

.story-card {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    opacity: 1;
    transition: all 0.5s ease-in-out
}

.story-card .icon {
    color: white;
    height: 200px;
    margin: 20px;
}

.story-card .content {
    color: white;
    font-size: 30px;
    flex: 1 1;
}

.story-card.offscreen.left {
    opacity: 0;
    transform: translateX(-1000px)
}

.story-card.offscreen.right {
    opacity: 0;
    transform: translateX(1000px)
}
